body {
  background-color: skyblue;
}

.app {
  background-color: rgb(0, 110, 153);
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

.app h2 {
  color: white;
  text-align: center;
  padding-top: 10px;
}

#to-do-form input {
  background-color: rgb(19, 31, 194);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}

#to-do-form input::placeholder {
  color: #fff;
}
#to-do-form input::focus {
  outline: none;
}
#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(192, 125, 0);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}
